import { useState, useEffect } from 'react';
import { Container, Typography, Box, Paper, TextField, Button, Grid, Snackbar, Alert, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Dialog, DialogTitle, DialogContent, DialogActions, List, ListItem, ListItemText, Drawer, Tooltip, IconButton } from '@mui/material';
import { KjeldahlData, saveToDatabase, getSavedData, deleteFromDatabase } from '../data/database';
import DeleteIcon from '@mui/icons-material/Delete';
import VisibilityIcon from '@mui/icons-material/Visibility';
import DownloadIcon from '@mui/icons-material/Download';

const aminoAcids = [
  'Alanine', 'Arginine', 'Asparagine', 'Aspartic acid', 'Cysteine',
  'Glutamic acid', 'Glutamine', 'Glycine', 'Histidine', 'Isoleucine',
  'Leucine', 'Lysine', 'Methionine', 'Phenylalanine', 'Proline',
  'Serine', 'Threonine', 'Tryptophan', 'Tyrosine', 'Valine'
];

interface AminoAcidInput {
  name: string;
  value: string;
  calculatedValue: number | null;
}

interface Citation {
  authors: string;
  year: string;
  title: string;
  journal: string;
  volume: string;
  issue: string;
  pages: string;
  doi: string;
}

const drawerWidth = 240;

export default function Home() {
  const [inputs, setInputs] = useState<AminoAcidInput[]>(
    aminoAcids.map(name => ({ name, value: '', calculatedValue: null }))
  );
  const [totalKjeldahl, setTotalKjeldahl] = useState('');
  const [citation, setCitation] = useState({
    authors: '',
    year: '',
    title: '',
    journal: '',
    volume: '',
    issue: '',
    pages: '',
    doi: ''
  });
  const [scientificName, setScientificName] = useState('');
  const [result, setResult] = useState<number | null>(null);
  const [savedEntries, setSavedEntries] = useState<KjeldahlData[]>([]);
  const [selectedEntry, setSelectedEntry] = useState<KjeldahlData | null>(null);
  const [deleteDialogOpen, setDeleteDialogOpen] = useState(false);
  const [entryToDelete, setEntryToDelete] = useState<KjeldahlData | null>(null);
  const [snackbar, setSnackbar] = useState({ open: false, message: '', severity: 'success' as 'success' | 'error' });
  const [isLoadingDOI, setIsLoadingDOI] = useState(false);

  useEffect(() => {
    loadSavedData();
  }, []);

  useEffect(() => {
    if (citation.doi) {
      fetchCitationFromDOI(citation.doi);
    }
  }, [citation.doi]);

  const loadSavedData = async () => {
    try {
      const data = await getSavedData();
      setSavedEntries(data);
    } catch (error) {
      console.error('Error loading data:', error);
      setSnackbar({
        open: true,
        message: 'Error loading saved entries',
        severity: 'error'
      });
    }
  };

  const handleInputChange = (index: number, value: string) => {
    const newInputs = [...inputs];
    newInputs[index] = { ...newInputs[index], value };
    
    if (value.includes('+') || value.includes('-') || value.includes('*') || value.includes('/')) {
      try {
        const result = Function(`'use strict'; return (${value})`)();
        newInputs[index].calculatedValue = isNaN(result) ? null : result;
      } catch {
        newInputs[index].calculatedValue = null;
      }
    } else {
      const numValue = parseFloat(value);
      newInputs[index].calculatedValue = isNaN(numValue) ? null : numValue;
    }
    
    setInputs(newInputs);
  };

  const calculateFactor = () => {
    if (!totalKjeldahl || !scientificName || !citation.doi) {
      setSnackbar({
        open: true,
        message: 'Please fill in all required fields',
        severity: 'error'
      });
      return;
    }

    const kjeldahlValue = parseFloat(totalKjeldahl);
    if (isNaN(kjeldahlValue) || kjeldahlValue <= 0) {
      setSnackbar({
        open: true,
        message: 'Kjeldahl Nitrogen must be a positive number',
        severity: 'error'
      });
      return;
    }

    let trueProtein = 0;
    let hasValidInputs = false;

    const aminoAcidData: Record<string, { molarWeight: number, waterFraction: number }> = {
      'Alanine': { molarWeight: 89.09, waterFraction: 18.015 },
      'Arginine': { molarWeight: 174.20, waterFraction: 18.015 },
      'Asparagine': { molarWeight: 132.12, waterFraction: 18.015 },
      'Aspartic acid': { molarWeight: 133.10, waterFraction: 18.015 },
      'Cysteine': { molarWeight: 121.16, waterFraction: 18.015 },
      'Glutamic acid': { molarWeight: 147.13, waterFraction: 18.015 },
      'Glutamine': { molarWeight: 146.15, waterFraction: 18.015 },
      'Glycine': { molarWeight: 75.07, waterFraction: 18.015 },
      'Histidine': { molarWeight: 155.16, waterFraction: 18.015 },
      'Isoleucine': { molarWeight: 131.17, waterFraction: 18.015 },
      'Leucine': { molarWeight: 131.17, waterFraction: 18.015 },
      'Lysine': { molarWeight: 146.19, waterFraction: 18.015 },
      'Methionine': { molarWeight: 149.21, waterFraction: 18.015 },
      'Phenylalanine': { molarWeight: 165.19, waterFraction: 18.015 },
      'Proline': { molarWeight: 115.13, waterFraction: 18.015 },
      'Serine': { molarWeight: 105.09, waterFraction: 18.015 },
      'Threonine': { molarWeight: 119.12, waterFraction: 18.015 },
      'Tryptophan': { molarWeight: 204.23, waterFraction: 18.015 },
      'Tyrosine': { molarWeight: 181.19, waterFraction: 18.015 },
      'Valine': { molarWeight: 117.15, waterFraction: 18.015 }
    };

    inputs.forEach(input => {
      if (input.calculatedValue !== null && input.calculatedValue > 0) {
        const acidData = aminoAcidData[input.name];
        if (acidData) {
          const proteinContribution = input.calculatedValue * (acidData.molarWeight - acidData.waterFraction) / acidData.molarWeight;
          trueProtein += proteinContribution;
          hasValidInputs = true;
        }
      }
    });

    if (!hasValidInputs) {
      setSnackbar({
        open: true,
        message: 'Please enter at least one valid amino acid value',
        severity: 'error'
      });
      return;
    }

    const trueProteinPercentage = trueProtein / 10;
    const factor = trueProteinPercentage / kjeldahlValue;
    setResult(Math.round(factor * 100) / 100);
  };

  const handleSave = async () => {
    if (!scientificName || !citation.doi || !totalKjeldahl || !result) {
      setSnackbar({
        open: true,
        message: 'Please fill in all required fields',
        severity: 'error'
      });
      return;
    }

    const data: KjeldahlData = {
      scientificName,
      aminoAcids: inputs.reduce((acc, input) => ({
        ...acc,
        [input.name]: input.calculatedValue
      }), {}),
      kjeldahlNitrogen: parseFloat(totalKjeldahl),
      factor: result,
      authors: citation.authors,
      year: citation.year,
      title: citation.title,
      journal: citation.journal,
      volume: citation.volume,
      issue: citation.issue,
      pages: citation.pages,
      doi: citation.doi,
      date: new Date().toISOString()
    };

    const success = await saveToDatabase(data);
    if (success) {
      setSnackbar({
        open: true,
        message: 'Entry saved successfully',
        severity: 'success'
      });
      setScientificName('');
      setCitation({
        authors: '',
        year: '',
        title: '',
        journal: '',
        volume: '',
        issue: '',
        pages: '',
        doi: ''
      });
      setTotalKjeldahl('');
      setResult(null);
      setInputs(aminoAcids.map(name => ({ name, value: '', calculatedValue: null })));
      loadSavedData();
    } else {
      setSnackbar({
        open: true,
        message: 'Error saving entry',
        severity: 'error'
      });
    }
  };

  const handleDelete = (entry: KjeldahlData) => {
    setEntryToDelete(entry);
    setDeleteDialogOpen(false);
  };

  const handleDeleteConfirm = async () => {
    if (entryToDelete?.id) {
      const success = await deleteFromDatabase(entryToDelete.id);
      if (success) {
        loadSavedData();
        setSnackbar({
          open: true,
          message: 'Entry deleted successfully',
          severity: 'success'
        });
      }
    }
    setDeleteDialogOpen(false);
    setEntryToDelete(null);
  };

  const fetchCitationFromDOI = async (doi: string) => {
    setIsLoadingDOI(true);
    try {
      const response = await fetch(`https://api.crossref.org/works/${encodeURIComponent(doi)}`);
      const data = await response.json();
      const work = data.message;
      
      const authors = work.author?.map((author: any) => 
        `${author.family}${author.given ? `, ${author.given.charAt(0)}.` : ''}`
      ).join(', ') || '';

      const newCitation: Citation = {
        authors,
        year: work.published?.['date-parts']?.[0]?.[0]?.toString() || '',
        title: work.title?.[0] || '',
        journal: work['container-title']?.[0] || '',
        volume: work.volume || '',
        issue: work.issue || '',
        pages: work.page || '',
        doi: work.DOI || doi
      };

      setCitation(newCitation);
      setSnackbar({
        open: true,
        message: 'Citation details retrieved successfully',
        severity: 'success'
      });
    } catch (error) {
      console.error('Error fetching DOI:', error);
      setSnackbar({
        open: true,
        message: 'Error retrieving citation details. Please enter manually.',
        severity: 'error'
      });
    } finally {
      setIsLoadingDOI(false);
    }
  };

  const formatCitation = (citation: Citation): string => {
    if (!citation.authors || !citation.year || !citation.title) return '';
    
    let formattedCitation = `${citation.authors} (${citation.year}). ${citation.title}`;
    if (citation.journal) {
      formattedCitation += `. ${citation.journal}`;
      if (citation.volume) {
        formattedCitation += `, ${citation.volume}`;
        if (citation.issue) {
          formattedCitation += `(${citation.issue})`;
        }
      }
      if (citation.pages) {
        formattedCitation += `, ${citation.pages}`;
      }
    }
    formattedCitation += '.';
    if (citation.doi) {
      formattedCitation += ` https://doi.org/${citation.doi}`;
    }
    return formattedCitation;
  };

  const formatHowToCite = () => {
    const currentYear = new Date().getFullYear();
    return `Somasundaram, T.C. (${currentYear}). Kjeldahl Nitrogen Factor Calculator [Software]. Zenodo. https://doi.org/10.5281/zenodo.15057026`;
  };

  const handleDownloadCitation = () => {
    const currentYear = new Date().getFullYear();
    const risContent = `TY  - COMP
AU  - Somasundaram, Thiru C.
PY  - ${currentYear}
TI  - Kjeldahl Nitrogen Factor Calculator
M3  - Software
PB  - Zenodo
DO  - 10.5281/zenodo.15057026
UR  - https://doi.org/10.5281/zenodo.15057026
ER  -`;

    const blob = new Blob([risContent], { type: 'application/x-research-info-systems' });
    const url = window.URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = 'citation.ris';
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    window.URL.revokeObjectURL(url);
  };

  return (
    <Box sx={{ display: 'flex', flexDirection: 'column', minHeight: '100vh' }}>
      <Box sx={{ display: 'flex', flex: 1 }}>
        <Container maxWidth="lg" sx={{ py: 2, marginRight: `${drawerWidth}px` }}>
          <Typography variant="h4" component="h1" gutterBottom align="center">
            Kjeldahl Nitrogen Factor Calculator
          </Typography>
          
          <Typography variant="subtitle1" align="center" gutterBottom>
            Created by Dr Thiru Chenduran Somasundaram, Deakin University
          </Typography>

          <Paper sx={{ p: 3, my: 2 }}>
            <Typography variant="h6" gutterBottom>
              Enter Data for Calculation
            </Typography>
            
            <Grid container spacing={2}>
              <Grid item xs={12}>
                <TextField
                  fullWidth
                  label="Scientific Name"
                  value={scientificName}
                  onChange={(e) => setScientificName(e.target.value)}
                  required
                  helperText="Enter the scientific name of the species"
                />
              </Grid>

              <Grid item xs={12}>
                <TextField
                  fullWidth
                  label="DOI"
                  value={citation.doi}
                  onChange={(e) => setCitation(prev => ({ ...prev, doi: e.target.value }))}
                  helperText={isLoadingDOI ? "Loading citation details..." : "Enter DOI to auto-fill citation details"}
                />
              </Grid>

              <Grid item xs={12}>
                <TextField
                  fullWidth
                  label="Total Kjeldahl Nitrogen (%)"
                  type="number"
                  value={totalKjeldahl}
                  onChange={(e) => setTotalKjeldahl(e.target.value)}
                  required
                  helperText="Enter the total Kjeldahl nitrogen percentage"
                />
              </Grid>

              <Grid item xs={12}>
                <Typography variant="subtitle1" gutterBottom>Amino Acid Composition (mg/g)</Typography>
                <Grid container spacing={2}>
                  {inputs.map((input, index) => (
                    <Grid item xs={12} sm={6} md={4} key={input.name}>
                      <TextField
                        fullWidth
                        label={input.name}
                        value={input.value}
                        onChange={(e) => handleInputChange(index, e.target.value)}
                        helperText={input.calculatedValue !== null ? `Calculated: ${input.calculatedValue.toFixed(2)}` : 'Optional'}
                      />
                    </Grid>
                  ))}
                </Grid>
              </Grid>

              <Grid item xs={12}>
                <Box sx={{ display: 'flex', gap: 2, justifyContent: 'center' }}>
                  <Button variant="contained" onClick={calculateFactor}>
                    Calculate Factor
                  </Button>
                  {result !== null && (
                    <Button variant="contained" onClick={handleSave}>
                      Save Data
                    </Button>
                  )}
                </Box>
              </Grid>
            </Grid>
          </Paper>

          {result !== null && (
            <Paper sx={{ p: 3, my: 3, boxShadow: 3 }}>
              <Typography variant="h6" gutterBottom sx={{ color: '#1a237e' }}>Results</Typography>
              <Typography variant="h5" gutterBottom sx={{ color: '#4a148c' }}>
                Kjeldahl Nitrogen Factor: {result.toFixed(2)}
              </Typography>
              <Typography sx={{ color: '#424242' }}>
                This factor can be used to convert Kjeldahl nitrogen measurements to protein content using:
                <Box component="span" sx={{ fontWeight: 'bold', display: 'block', mt: 1 }}>
                  Protein Content (%) = Kjeldahl Nitrogen (%)  {result.toFixed(2)}
                </Box>
              </Typography>
            </Paper>
          )}

          {savedEntries.length > 0 && (
            <Paper sx={{ p: 3, my: 3, boxShadow: 3 }}>
              <Typography variant="h6" gutterBottom sx={{ color: '#1a237e' }}>Food or Feed</Typography>
              <TableContainer>
                <Table>
                  <TableHead>
                    <TableRow>
                      <TableCell sx={{ fontWeight: 'bold', color: '#1a237e' }}>Scientific Name</TableCell>
                      <TableCell sx={{ fontWeight: 'bold', color: '#1a237e' }}>Kjeldahl N (%)</TableCell>
                      <TableCell sx={{ fontWeight: 'bold', color: '#1a237e' }}>Factor</TableCell>
                      <TableCell sx={{ fontWeight: 'bold', color: '#1a237e' }}>Reference</TableCell>
                      <TableCell sx={{ fontWeight: 'bold', color: '#1a237e' }}>Actions</TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {savedEntries.map((entry, index) => (
                      <TableRow key={entry.id} hover>
                        <TableCell>{entry.scientificName}</TableCell>
                        <TableCell>{entry.kjeldahlNitrogen.toFixed(2)}</TableCell>
                        <TableCell>{entry.factor.toFixed(2)}</TableCell>
                        <TableCell>
                          <Box component="span" sx={{ display: 'flex', alignItems: 'start' }}>
                            {entry.date}
                            <Typography component="sup" sx={{ ml: 0.5, fontSize: '0.7em' }}>
                              [{index + 1}]
                            </Typography>
                          </Box>
                        </TableCell>
                        <TableCell>
                          <Tooltip title="View Details">
                            <IconButton
                              size="small"
                              onClick={() => setSelectedEntry(entry)}
                              sx={{ color: '#1a237e' }}
                            >
                              <VisibilityIcon />
                            </IconButton>
                          </Tooltip>
                          <Tooltip title="Delete Entry">
                            <IconButton
                              size="small"
                              onClick={() => handleDelete(entry)}
                              sx={{ color: '#d32f2f' }}
                            >
                              <DeleteIcon />
                            </IconButton>
                          </Tooltip>
                        </TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </TableContainer>

              <Box sx={{ mt: 3 }}>
                <Typography variant="h6" gutterBottom sx={{ color: '#1a237e' }}>References</Typography>
                {savedEntries.map((entry, index) => (
                  <Typography key={entry.id} sx={{ mb: 1, pl: 3, textIndent: '-1.5em' }}>
                    [{index + 1}] {formatCitation({
                      authors: entry.authors,
                      year: entry.year,
                      title: entry.title,
                      journal: entry.journal,
                      volume: entry.volume,
                      issue: entry.issue,
                      pages: entry.pages,
                      doi: entry.doi
                    })}
                  </Typography>
                ))}
              </Box>
            </Paper>
          )}

          <Paper sx={{ p: 3, my: 3, boxShadow: 3 }}>
            <Typography variant="h6" gutterBottom sx={{ color: '#1a237e' }}>How to Cite</Typography>
            <Typography paragraph sx={{ fontFamily: 'monospace', whiteSpace: 'pre-wrap' }}>
              {formatHowToCite()}
            </Typography>
            <Button 
              variant="outlined" 
              onClick={handleDownloadCitation}
              startIcon={<DownloadIcon />}
              sx={{ mt: 1 }}
            >
              Download Citation
            </Button>
          </Paper>
        </Container>

        <Drawer
          variant="permanent"
          anchor="right"
          sx={{
            width: drawerWidth,
            flexShrink: 0,
            '& .MuiDrawer-paper': {
              width: drawerWidth,
              boxSizing: 'border-box',
              padding: 1,
              bgcolor: '#fafafa',
              borderLeft: '1px solid rgba(0, 0, 0, 0.12)',
              mt: '48px'
            },
          }}
        >
          <Typography variant="h6" sx={{ mb: 1 }}>
            Food or Feed ({savedEntries.length})
          </Typography>
          <List dense>
            {savedEntries.map((entry, index) => (
              <ListItem 
                key={entry.id}
                button
                onClick={() => setSelectedEntry(entry)}
                sx={{ mb: 0.5 }}
              >
                <ListItemText
                  primary={
                    <Box component="span" sx={{ display: 'flex', alignItems: 'start' }}>
                      {`${index + 1}. ${entry.scientificName}`}
                      <Typography component="sup" sx={{ ml: 0.5, fontSize: '0.7em' }}>
                        [{index + 1}]
                      </Typography>
                    </Box>
                  }
                  secondary={`Factor: ${entry.factor.toFixed(2)}`}
                />
              </ListItem>
            ))}
          </List>
        </Drawer>
      </Box>

      <Box 
        component="footer" 
        sx={{ 
          py: 3, 
          px: 2, 
          mt: 'auto',
          backgroundColor: '#f5f5f5',
          borderTop: '1px solid #e0e0e0'
        }}
      >
        <Container maxWidth="lg">
          <Typography variant="body2" color="text.secondary" align="center">
            Created by Dr Thiru Chenduran Somasundaram, Deakin University
          </Typography>
          <Typography variant="body2" color="text.secondary" align="center" sx={{ mt: 1 }}>
            Somasundaram, T.C. ({new Date().getFullYear()}). Kjeldahl Nitrogen Factor Calculator [Web application]. 
            Deakin University. https://DrThiruCSomasundaram.github.io/Kjeldahl/
          </Typography>
        </Container>
      </Box>

      <Dialog
        open={!!selectedEntry}
        onClose={() => setSelectedEntry(null)}
        maxWidth="md"
        fullWidth
      >
        <DialogTitle>
          Details for {selectedEntry?.scientificName}
        </DialogTitle>
        <DialogContent>
          <Typography variant="subtitle1" gutterBottom>
            Citation: {selectedEntry && formatCitation({
              authors: selectedEntry.authors,
              year: selectedEntry.year,
              title: selectedEntry.title,
              journal: selectedEntry.journal,
              volume: selectedEntry.volume,
              issue: selectedEntry.issue,
              pages: selectedEntry.pages,
              doi: selectedEntry.doi
            })}
          </Typography>
          <Typography variant="subtitle1" gutterBottom>
            Amino Acid Composition (mg/g):
          </Typography>
          <Grid container spacing={2}>
            {selectedEntry && Object.entries(selectedEntry.aminoAcids).map(([acid, value]) => (
              <Grid item xs={6} key={acid}>
                <Typography>
                  {acid}: {value !== null ? value.toFixed(2) : 'Not specified'}
                </Typography>
              </Grid>
            ))}
          </Grid>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setSelectedEntry(null)}>Close</Button>
        </DialogActions>
      </Dialog>

      <Dialog
        open={deleteDialogOpen}
        onClose={() => setDeleteDialogOpen(false)}
      >
        <DialogTitle>Confirm Delete</DialogTitle>
        <DialogContent>
          Are you sure you want to delete this entry?
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setDeleteDialogOpen(false)}>Cancel</Button>
          <Button onClick={handleDeleteConfirm} color="error">Delete</Button>
        </DialogActions>
      </Dialog>

      <Snackbar
        open={snackbar.open}
        autoHideDuration={6000}
        onClose={() => setSnackbar(prev => ({ ...prev, open: false }))}
      >
        <Alert
          onClose={() => setSnackbar(prev => ({ ...prev, open: false }))}
          severity={snackbar.severity}
        >
          {snackbar.message}
        </Alert>
      </Snackbar>
    </Box>
  );
} 