import { collection, addDoc, getDocs, deleteDoc, doc, query, orderBy } from 'firebase/firestore';
import { db } from '../config/firebase';

export interface KjeldahlData {
  id?: string;
  scientificName: string;
  aminoAcids: Record<string, number | null>;
  kjeldahlNitrogen: number;
  factor: number;
  authors: string;
  year: string;
  title: string;
  journal: string;
  volume: string;
  issue: string;
  pages: string;
  doi: string;
  date: string;
}

export const saveToDatabase = async (data: KjeldahlData): Promise<boolean> => {
  try {
    const docRef = await addDoc(collection(db, 'kjeldahlData'), {
      ...data,
      date: new Date().toISOString()
    });
    return !!docRef.id;
  } catch (error) {
    console.error('Error saving to database:', error);
    return false;
  }
};

export const getSavedData = async (): Promise<KjeldahlData[]> => {
  try {
    const q = query(collection(db, 'kjeldahlData'), orderBy('date', 'desc'));
    const querySnapshot = await getDocs(q);
    return querySnapshot.docs.map(doc => ({
      id: doc.id,
      ...doc.data()
    } as KjeldahlData));
  } catch (error) {
    console.error('Error getting data from database:', error);
    return [];
  }
};

export const deleteFromDatabase = async (id: string): Promise<boolean> => {
  try {
    await deleteDoc(doc(db, 'kjeldahlData', id));
    return true;
  } catch (error) {
    console.error('Error deleting from database:', error);
    return false;
  }
}; 